.heading{
  font-family: "neue-haas-grotesk-display", sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: 60px;
}
.about-me-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
}

.about-me-image {
  width: 40%;
  opacity: 0;
  transform: translateX(-100%);
  transition: transform 1s ease-out, opacity 1s ease-out;
}

.about-me-text {
  width: 50%;
  opacity: 0;
  transform: translateX(100%);
  transition: transform 1s ease-out, opacity 1s ease-out;
  font-family: "neue-haas-grotesk-display", sans-serif;
  font-weight: 20;
  font-style: normal;
  font-size: 20px;
  margin-right: 25px;
}

.animate .about-me-image {
  transform: translateX(0);
  opacity: 1;
}

.animate .about-me-text {
  transform: translateX(0);
  opacity: 1;
}
